*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}
#home{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}
.footer{
    top: 100vh;
    position: absolute;
    width: 100%;
    background-color: black;
    -webkit-align-items: center;
            align-items: center;
}
.text-center{
    color: white;
    padding-top: 30px;
    padding-bottom: 30px;
}
.container{
    position: relative;
}
.container .navigation
{
    position: relative;
    width: 20px;
    height: 100vh;
    background-color: #fff;
    border-bottom-left-radius: 15px;
    border-top-left-radius: 15px;
    overflow: hidden;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
    transition: 0.6s;
    opacity: 0.5;
}
.container .navigation.active{
    width: 200px;
    opacity: 0.6;
}
.container .navigation ul{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    
}
.container .navigation ul a:hover{
    background-color: dimgray;
    color: #fff;
}
.container .navigation ul li{
    position: relative;
    list-style: none;
    width: 100%;
    left:  0;
}
.container .navigation ul li:hover{

    transition: 0.5s;
}
.container .navigation ul a{
    position: relative;
    display: block;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    text-decoration: none;
    color: #000000;
    font-weight: 500;
    transition: 0.5s;
}
.container .navigation ul a .icon{
    position: relative;
    display: block;
    min-width: 80px;
    height: 60px;
    line-height: 60px;
    text-align: center;
}
.container .navigation ul a .icon .fas{
    font-size: 24px;
}
.container .navigation ul li a .title{
    position: relative;
    display: block;
    height: 60px;
    line-height: 60px;
    white-space: nowrap;
}
.toggle{
    position: absolute;
    top: 50%;
    left: -20px;
    width: 40px;
    height: 40px;
    background: #fff;
    cursor: pointer;
    border: 5px solid rgba(255, 255, 255, .1);
    border-radius: 50%;
    opacity: 0.8;
}
.toggle::before{
    content: '<';
    font-weight: bold;
    position: absolute;
    width: 100%;
    height: 100%;
    text-align: center;
    line-height: 30px;
    font-size: 30px;
    color: #000000;
    

}
.toggle.active::before{
    content: ">";
    font-weight: bold;

}


/* Media for mobiles (Big Screen) */

@media screen and (max-width: 620px){
    .container .navigation
{
    position: relative;
    width: 20px;
    opacity: 0.3;
    overflow: hidden;
}
.container .navigation
{
    overflow: hidden;
    opacity: 0.2;
}
.container .navigation.active{
    opacity: 100;
}

.container .navigation ul a:hover{
    color: #000000;
}
}
