@import url('https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap');

*{
  margin: 0;
  padding: 0;
  text-decoration: none;
  font-family: "Poppins",sans-serif;
}

#home{
  width: 100%;
  height: 100vh;
  background-position-x: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(./images/2.JPG);
}

.content{
  position: relative;
  top: 0;
  left: 30px;
  height: 100vh;
}

.logo{
  margin-top: 30px;
  margin-left: 30px;
  padding: 10px 10px;
  width: 48px;
  height: 36px;
  background-image: url(./images/white_logo.png);
  background-size: contain;
}


#home .content h1
{
  margin-top: 30%;
  font-size: 50px;
  margin-left: 30px;
  color: #fff;
  align-items: center;
}

#home .content h2
{
  margin-left: 30px;
  font-size: 60px;
  align-items: center;
  color: #fff;
}


#home .content h2::after{
  content: "AmiT";
  animation: textanim 10s linear infinite;
}

@keyframes textanim{
  25%{
    content: "an Engineer";
  }
  50%{
    content: "a Developer";
  }
  75%{
    content: "a Designer";
  }
}

.sm a{
  margin-top: 35px;
  margin-left: 30px;
  color: #fff;
  font-size: 26px;
  transition: .2s linear;
}

.sm a:hover{
  transform: rotate(360deg);
}

.sm a:nth-child(2){
  color: #1DA1F2;
}
.sm a:nth-child(5){
  color: #FF0000;
}

#home .buttons{
  margin-top: 50px;
}

#home .buttons a{
  font-weight: bold;
  margin: 15px 22px;
  color: #fff;
  background-color: dimgray;
  font-size: 16px;
  font-weight: 600;
  width: 180px;
  border: 0px solid #000000; 
  padding: 16px 35px;
  border-radius: 10px;
  transition: .2s linear;
}
#home .buttons a:nth-child(1)
{
  padding: 16px 38px;
}

#home .buttons a:hover
{
  background-color: #c73e1d;
  color: #fff;

}

#home .dapp{
  margin-top: 50px;
}

#home .dapp a{
  font-weight: bold;
  margin: 15px 22px;
  color: #fff;
  background-color: dimgray;
  font-size: 16px;
  font-weight: 600;
  width: 180px;
  border: 0px solid #000000; 
  padding: 16px 28px;
  border-radius: 10px;
  transition: .2s linear;
}

#home .dapp a:hover
{
  background-color: #c73e1d;
}

/* Media for mobiles (Big Screen) */

@media screen and (max-width: 620px){

  #home .content h1
{
  margin-top: 55%;
  font-size: 40px;
  margin-left: 10px;
  color: #fff;
  align-items: center;
}
#home .content h2
{
  margin-left: 10px;
  font-size: 50px;
  align-items: center;
  color: #fff;
}

#home .buttons{
  margin-top: 80px;
}

#home .buttons a{
  margin: 15px 10px;
  font-size: 16px;
  width: 150px;
  padding: 16px 20px;
}
#home .buttons a:nth-child(1)
{
  padding: 16px 20x;
}


#home .dapp{
  margin-top: 55px;
}

#home .dapp a{
  margin: 15px 10px;
  width: 150px; 
  padding: 16px 28px;

}

}